:local(.content)
{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 300px;
}


:local(.flex)
{
  display: flex;
  flex-direction: column;
  
  margin-top: 10px;
  margin-bottom: 90px;
  margin-left: 110px;
  margin-right: 110px;
}
:local(.head)
{
  display: flex;
  border: 1px solid black;
  overflow: hidden;
  border-radius: 20px;
}

:local(.head>*)
{
  position: relative;
  text-align: center;
  color: white;
  width: 100px;
  height: 40px;
  text-decoration: none;
  padding: 30px;
}
:local(.head>*>span)
{
  color: white;
  position: absolute;
  opacity: 0.5;
}

:local(.head>:first-child){
  background-color: rgb(255, 50, 50);
}

:local(.head>:last-child){
  background-color: rgb(51, 51, 51);
}

:local(.head>*:hover)
{
  transform: scale(1.05);
}

:local(.foot)
{
  transform: scale(0.8);
  display: flex;
  justify-content: center;
}

