/**/

.centered
{
  position: absolute;
  top:50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
.relative{position: relative;}

.blurable
{
  transition: 0.5s -webkit-filter ease-in-out;
  transition: 0.5s filter ease-in-out;
  transition: 0.5s filter ease-in-out, 0.5s -webkit-filter ease-in-out;
}

.blured
{
  pointer-events: none;
  -webkit-filter: drop-shadow(0px 0px 10px black) blur(4px);
          filter: drop-shadow(0px 0px 10px black) blur(4px);
}

.nopointer
{
  pointer-events: none;
}

.hoverable:hover{
  cursor: pointer;
  opacity: 0.6;
}
.scale-hoverable{transition: scale 0.2s ease-in-out;}
.scale-hoverable:hover{
  scale:1.05;
}

.fade {
  -webkit-animation: fade-in-keyframes 0.5s;
          animation: fade-in-keyframes 0.5s;
}
@-webkit-keyframes fade-in-keyframes {
  from {opacity: 0}
  to {opacity: 1}
}
@keyframes fade-in-keyframes {
  from {opacity: 0}
  to {opacity: 1}
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */ /* Internet Explorer/Edge */
            user-select: none;
}

.tooltip .tooltiptext {
  font-size: 20px;
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 110%;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent black transparent transparent;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}


