/* SWITCH */
.switch {
position: relative;
display: inline-block;
width: 60px;
height: 34px;
margin-left: 25px;
}

.switch input { 
opacity: 0;
width: 0;
height: 0;
}

.slider {
position: absolute;
cursor: pointer;
top: 0;
left: 0;
right: 0;
bottom: 0;
background-color: #4b4b4b6b;
transition: .4s;
}

.slider:before {
position: absolute;
content: "";
height: 26px;
width: 26px;
left: 4px;
bottom: 4px;
background-color: white;
transition: .4s;
}

input:checked + .slider.round:before
{
    content: "\1F319";
}

input:focus + .slider {
box-shadow: 0 0 1px #2196F3;
}

.switch input:checked + .slider:before {
-webkit-transform: translateX(26px);
transform: translateX(26px);
}


.slider.round {
border-radius: 34px;
}

.slider.round:before {
    content: "\2600\FE0F";
    top:-4px;
    left:-4px;
    background-color: transparent;
border-radius: 50%;
}
